import { useState, useRef, useEffect } from "react";
import PropTypes from "prop-types";
import DropdownStyles from "./styles/Dropdown.styles";

/**
 * A custom, self-contained Dropdown component.
 */
const Dropdown = ({ title, icon, children }) => {
  const [isOpen, setIsOpen] = useState(false);
  const dropdownRef = useRef(null);

  // Effect to close the dropdown when clicking outside of it
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setIsOpen(false);
      }
    };
    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  return (
    <>
      <DropdownStyles />
      <div className="forma-text-dropdown" ref={dropdownRef}>
        <button
          type="button"
          title={title}
          onClick={() => setIsOpen(!isOpen)}
          onMouseDown={(e) => e.preventDefault()} // Prevents editor from losing focus
          className="forma-text-dropdown-toggle"
          aria-expanded={isOpen}
        >
          {icon}
        </button>
        <div
          className={`forma-text-dropdown-menu ${
            isOpen ? "forma-text-dropdown-menu--show" : ""
          }`}
          onClick={(e) => {
            // Close dropdown if a button inside is clicked, allowing other content to remain open
            if (e.target.closest("button")) {
              setIsOpen(false);
            }
          }}
        >
          {children}
        </div>
      </div>
    </>
  );
};

Dropdown.propTypes = {
  title: PropTypes.string.isRequired,
  icon: PropTypes.node.isRequired,
  children: PropTypes.node.isRequired,
};

export default Dropdown;
