import { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { Link, Pilcrow, ExternalLink } from "lucide-react";
import BaseModal from "./BaseModal";
import LinkModalStyles from "./styles/LinkModal.styles";

const LinkModal = ({ isOpen, onClose, onApply, initialData, isEditing }) => {
  const [linkData, setLinkData] = useState({ text: "", href: "" });

  useEffect(() => {
    if (isOpen) {
      setLinkData(initialData);
    }
  }, [isOpen, initialData]);

  if (!isOpen) return null;

  return (
    <>
  <LinkModalStyles/>
      <BaseModal
        onClose={onClose}
        title={isEditing ? "Edit Link" : "Add Link"}
        icon={<Link size={20} />}
      >
        <div className="forma-text-link-modal-body">
          <div className="forma-text-link-form-group">
            <label className="forma-text-link-label">Link Text</label>
            <div className="forma-text-link-input-group">
              <span className="forma-text-link-input-icon">
                <Pilcrow size={16} />
              </span>
              <input
                type="text"
                className="forma-text-link-input"
                value={linkData.text}
                onChange={(e) =>
                  setLinkData({ ...linkData, text: e.target.value })
                }
              />
            </div>
          </div>
          <div className="forma-text-link-form-group">
            <label className="forma-text-link-label">Link URL</label>
            <div className="forma-text-link-input-group">
              <span className="forma-text-link-input-icon">
                <ExternalLink size={16} />
              </span>
              <input
                type="url"
                className="forma-text-link-input"
                value={linkData.href}
                onChange={(e) =>
                  setLinkData({ ...linkData, href: e.target.value })
                }
                placeholder="https://example.com"
                autoFocus
              />
            </div>
          </div>
        </div>
        <div className="forma-text-link-modal-footer">
          <button
            type="button"
            className="forma-text-link-button forma-text-link-button--secondary"
            onClick={onClose}
          >
            Cancel
          </button>
          <button
            type="button"
            className="forma-text-link-button forma-text-link-button--primary"
            onClick={() => onApply(linkData)}
          >
            Apply Link
          </button>
        </div>
      </BaseModal>
    </>
  );
};

LinkModal.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onApply: PropTypes.func.isRequired,
  initialData: PropTypes.shape({
    text: PropTypes.string,
    href: PropTypes.string,
  }).isRequired,
  isEditing: PropTypes.bool.isRequired,
};

export default LinkModal;