const BaseModalStyles = () => (
  <style>{`
        .forma-text-modal-overlay {
          position: fixed;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background-color: rgba(0, 0, 0, 0.5);
          display: flex;
          align-items: center;
          justify-content: center;
          z-index: 1050;
        }
        .forma-text-modal-dialog {
          background-color: #fff;
          border-radius: 0.5rem;
          box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
          width: 90%; /* More responsive on small screens */
          max-width: var(--forma-text-modal-width, 500px); /* Use CSS variable */
          display: flex;
          flex-direction: column;
          max-height: 90vh;
        }
        .forma-text-modal-header {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 1rem;
          border-bottom: 1px solid #dee2e6;
          background-color: #f8f9fa;
          border-top-left-radius: 0.5rem;
          border-top-right-radius: 0.5rem;
        }
        .forma-text-modal-title {
          margin: 0;
          font-size: 1.25rem;
          font-weight: 500;
          display: flex;
          align-items: center;
          gap: 0.5rem;
        }
        .forma-text-modal-close-button {
          background: transparent;
          border: none;
          font-size: 1.5rem;
          font-weight: 700;
          color: #000;
          opacity: 0.5;
          cursor: pointer;
          padding: 0.5rem;
          line-height: 1;
          transition: opacity 0.15s ease-in-out;
        }
        .forma-text-modal-close-button:hover {
          opacity: 0.75;
        }
        .forma-text-modal-content {
          overflow-y: auto;
        }
      `}</style>
);

export default BaseModalStyles;
