import PropTypes from "prop-types";
import { Smile } from "lucide-react";
import Dropdown from "./Dropdown";
import EmojiPickerStyles from "./styles/EmojiPicker.styles";

const EMOJIS = [
  "💻",
  "🖥️",
  "🖱️",
  "⌨️",
  "🖨️",
  "📱",
  "📲",
  "🕹️",
  "🧑‍💻",
  "👨‍💻",
  "👩‍💻",
  "🔧",
  "🔨",
  "⚙️",
  "🧰",
  "📡",
  "🌐",
  "🔌",
  "💾",
  "📂",
  "📁",
  "🗂️",
  "📝",
  "📊",
  "📈",
  "📉",
  "🔒",
  "🔓",
  "🛡️",
  "🐞",
  "⚡",
  "🚀",
  "🧪",
  "💡",
  "🤖",
  "🧩",
  "🧬",
  "🛠️",
  "📧",
  "🔍",
  "📥",
  "📤",
];
/**
 * An emoji picker dropdown.
 */
const EmojiPicker = ({ applyStyle }) => (
  <>
    <EmojiPickerStyles />
    <Dropdown title="Insert Emoji" icon={<Smile size={16} />}>
      <div className="forma-text-emoji-grid">
        {EMOJIS.map((emoji) => (
          <button
            key={emoji}
            className="forma-text-emoji-button"
            onClick={() => applyStyle("insertText", emoji)}
          >
            {emoji}
          </button>
        ))}
      </div>
    </Dropdown>
  </>
);

EmojiPicker.propTypes = {
  applyStyle: PropTypes.func.isRequired,
};

export default EmojiPicker;
