import React from "react";
import PropTypes from "prop-types";

/**
 * A reusable, memoized button component for the editor status bar.
 */
const StatusbarButton = React.memo(
  ({ onClick, title, command, activeStyles, disabled = false, children }) => {
    // Active state styles are applied dynamically via the style prop
    const activeStyle = {
      color: "#fff",
      backgroundColor: "#6c757d",
      borderColor: "#6c757d",
    };

    const finalStyle =
      activeStyles && activeStyles.has(command) ? activeStyle : {};

    return (
      <>
        <style>
          {`
          .forma-text-statusbar-button {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            padding: 0.375rem 0.75rem;
            font-size: 1rem;
            font-weight: 400;
            line-height: 1.5;
            text-align: center;
            vertical-align: middle;
            cursor: pointer;
            user-select: none;
            border: 1px solid #6c757d;
            background-color: transparent;
            color: #6c757d;
            border-radius: 0.25rem;
            transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
          }
          .forma-text-statusbar-button:hover {
            color: #fff;
            background-color: #6c757d;
            border-color: #6c757d;
          }
          .forma-text-statusbar-button:disabled {
            pointer-events: none;
            opacity: 0.65;
          }
        `}
        </style>
        <button
          type="button"
          onClick={onClick}
          onMouseDown={(e) => e.preventDefault()}
          className="forma-text-statusbar-button"
          title={title}
          disabled={disabled}
          style={finalStyle}
        >
          {children}
        </button>
      </>
    );
  }
);

StatusbarButton.propTypes = {
  onClick: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  command: PropTypes.string,
  activeStyles: PropTypes.object,
  disabled: PropTypes.bool,
  children: PropTypes.node.isRequired,
};

StatusbarButton.displayName = "FormaTextStatusbarButton";

export default StatusbarButton;
