const ColorPickerStyles = () => (
  <style>{`
        .forma-text-color-grid {
          width: 160px;
          padding: 0.5rem;
          display: flex;
          flex-wrap: wrap;
          gap: 4px;
        }
        .forma-text-color-swatch {
          width: 24px;
          height: 24px;
          border-radius: 50%;
          border: 1px solid #ddd;
          padding: 0;
          cursor: pointer;
          transition: transform 0.1s ease-in-out;
        }
        .forma-text-color-swatch:hover {
          transform: scale(1.1);
        }
      `}</style>


)
export default ColorPickerStyles;
