import { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { CodeXml, Maximize, Minimize, Sigma, Binary } from "lucide-react";

const FuturisticStatusBar = ({
  wordCount,
  charCount,
  onToggleSource,
  isFullScreen,
  onToggleFullScreen,
}) => {
  const [isBooted, setIsBooted] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => setIsBooted(true), 100);
    return () => clearTimeout(timer);
  }, []);

  const glowColor = "#00ffff";
  const borderColor = "rgba(138, 43, 226, 0.4)";
  const textColor = "#e0e0e0";

  const statusBarStyle = {
    position: "fixed",
    bottom: "0px",
    left: "50%",
    width: "100%",
    zIndex: 1999,
    fontFamily: '"Orbitron", "Segoe UI", sans-serif',
    background: "rgba(15, 15, 25, 0.5)",
    backdropFilter: "blur(16px) saturate(200%)",
    color: textColor,
    border: `1px solid ${borderColor}`,

    overflow: "hidden",
    opacity: isBooted ? 1 : 0,
    transform: isBooted
      ? "translateX(-50%) translateY(0)"
      : "translateX(-50%) translateY(20px)",
    transition: "opacity 0.8s ease-out, transform 0.8s ease-out",
  };

  // --- Decorative and Layout Styles ---
  const gridBackgroundStyle = {
    position: "absolute",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundImage: `linear-gradient(${glowColor} 1px, transparent 1px), linear-gradient(to right, ${glowColor} 1px, transparent 1px)`,
    backgroundSize: "40px 40px",
    opacity: 0.05,
    animation: "forma-text-move-grid 20s linear infinite",
    zIndex: 1,
  };

  const statusbarContentStyle = {
    position: "relative",
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    padding: "10px 25px",
    zIndex: 2,
  };

  const cornerBracketBaseStyle = {
    position: "absolute",
    width: "20px",
    height: "20px",
    borderStyle: "solid",
    borderColor: glowColor,
    opacity: isBooted ? 0.5 : 0,
    transition: "opacity 1s ease 0.5s",
  };

  const cornerStyles = {
    topLeft: {
      ...cornerBracketBaseStyle,
      top: "-2px",
      left: "-2px",
      borderWidth: "2px 0 0 2px",
      borderRadius: "8px 0 0 0",
    },
    topRight: {
      ...cornerBracketBaseStyle,
      top: "-2px",
      right: "-2px",
      borderWidth: "2px 2px 0 0",
      borderRadius: "0 8px 0 0",
    },
    bottomLeft: {
      ...cornerBracketBaseStyle,
      bottom: "-2px",
      left: "-2px",
      borderWidth: "0 0 2px 2px",
      borderRadius: "0 0 0 8px",
    },
    bottomRight: {
      ...cornerBracketBaseStyle,
      bottom: "-2px",
      right: "-2px",
      borderWidth: "0 2px 2px 0",
      borderRadius: "0 0 8px 0",
    },
  };

  // --- Content Styles ---
  const statusGroupStyle = {
    display: "flex",
    alignItems: "center",
    gap: "24px",
  };

  const dataReadoutStyle = {
    display: "flex",
    alignItems: "center",
    gap: "8px",
    color: "#aab5c3",
  };

  const dataIconStyle = {
    color: glowColor,
    filter: `drop-shadow(0 0 3px ${glowColor})`,
  };

  const dataLabelStyle = {
    fontSize: "12px",
    fontWeight: 600,
    letterSpacing: "1px",
    opacity: 0.6,
  };

  const dataValueStyle = {
    fontFamily: '"Roboto Mono", monospace',
    fontSize: "16px",
    fontWeight: 700,
    color: "white",
  };

  const actionButtonStyle = {
    background: "transparent",
    border: "1px solid transparent",
    color: "#aab5c3",
    width: "36px",
    height: "36px",
    display: "inline-flex",
    alignItems: "center",
    justifyContent: "center",
    borderRadius: "50%",
    cursor: "pointer",
    position: "relative",
    transition: "all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1)",
  };

  return (
    <>
      <style>
        {`
    /* Styles that cannot be inlined: animations, pseudo-elements, and pseudo-classes */
        @keyframes forma-text-move-grid {
          from { background-position: 0 0; }
          to { background-position: 40px 40px; }
        }

        .forma-text-action-button::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          border-radius: 50%;
          border: 1px solid ${borderColor};
          transition: all 0.3s ease;
        }

        .forma-text-action-button:hover {
          color: white;
        }

        .forma-text-action-button:hover::before {
          transform: scale(1.2);
          opacity: 0;
        }

        .forma-text-action-button:active {
          transform: scale(0.9);
        }
   `}
      </style>

      <div className="forma-text-futuristic-statusbar" style={statusBarStyle}>
        {/* Decorative Elements */}
        <div
          className="forma-text-corner-bracket"
          style={cornerStyles.topLeft}
        ></div>
        <div
          className="forma-text-corner-bracket"
          style={cornerStyles.topRight}
        ></div>
        <div
          className="forma-text-corner-bracket"
          style={cornerStyles.bottomLeft}
        ></div>
        <div
          className="forma-text-corner-bracket"
          style={cornerStyles.bottomRight}
        ></div>
        <div
          className="forma-text-grid-background"
          style={gridBackgroundStyle}
        ></div>

        <div
          className="forma-text-statusbar-content"
          style={statusbarContentStyle}
        >
          {/* Left Segment */}
          <div className="forma-text-status-group" style={statusGroupStyle}>
            <div
              className="forma-text-data-readout"
              title="Word Count"
              style={dataReadoutStyle}
            >
              <Sigma size={16} style={dataIconStyle} />
              <span style={dataLabelStyle}>WORDS</span>
              <span style={dataValueStyle}>
                {wordCount.toString().padStart(4, "0")}
              </span>
            </div>
            <div
              className="forma-text-data-readout"
              title="Character Count"
              style={dataReadoutStyle}
            >
              <Binary size={16} style={dataIconStyle} />
              <span style={dataLabelStyle}>CHARS</span>
              <span style={dataValueStyle}>
                {charCount.toString().padStart(5, "0")}
              </span>
            </div>
          </div>

          {/* Right Segment */}
          <div className="forma-text-status-group" style={statusGroupStyle}>
            <button
              className="forma-text-action-button"
              title="View HTML Source"
              onClick={onToggleSource}
              style={actionButtonStyle}
            >
              <CodeXml size={18} />
            </button>
            <button
              className="forma-text-action-button"
              title={isFullScreen ? "Exit Full Screen" : "Full Screen"}
              onClick={onToggleFullScreen}
              style={actionButtonStyle}
            >
              {isFullScreen ? <Minimize size={18} /> : <Maximize size={18} />}
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

FuturisticStatusBar.propTypes = {
  wordCount: PropTypes.number.isRequired,
  charCount: PropTypes.number.isRequired,
  onToggleSource: PropTypes.func.isRequired,
  onToggleFullScreen: PropTypes.func.isRequired,
  isFullScreen: PropTypes.bool.isRequired,
};

export default FuturisticStatusBar;
