const ToolbarButtonStyles = () => (
  <style>{`
          .forma-text-toolbar-button {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            padding: 0.375rem 0.75rem;
            font-size: 1rem;
            font-weight: 400;
            line-height: 1.5;
            color: #6c757d;
            background-color: transparent;
            border: 1px solid #6c757d;
            border-radius: 0.25rem;
            cursor: pointer;
            user-select: none;
            transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
          }

          .forma-text-toolbar-button:hover {
            color: #fff;
            background-color: #6c757d;
            border-color: #6c757d;
          }

          .forma-text-toolbar-button--active {
            color: #fff;
            background-color: #5a6268;
            border-color: #545b62;
          }

          .forma-text-toolbar-button:disabled {
            pointer-events: none;
            opacity: 0.65;
          }




/* --- Custom Tooltip Styles --- */

/* 1. The container (no changes needed here) */
.forma-text-tooltip-container {
  position: relative;
  display: inline-block;
}

/* 2. The tooltip bubble */
.forma-text-tooltip {
  visibility: hidden;
  opacity: 0;
  width: max-content;
  background-color: #212529;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 6px 12px;
  font-size: 12px;
  font-weight: 500;
  
  /* --- CHANGED --- */
  /* Position it BELOW the button */
  position: absolute;
  z-index: 10;
  top: 130%; /* Changed from 'bottom' */
  left: 50%;
  transform: translateX(-50%);
  
  transition: opacity 0.2s ease-in-out;
}

/* 3. The small arrow pointing UP to the button */
.forma-text-tooltip::after {
  content: "";
  position: absolute;
  
  /* --- CHANGED --- */
  bottom: 100%; /* Position arrow at the TOP of the tooltip */
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  /* Flip the arrow direction to point up */
  border-color: transparent transparent #212529 transparent;
}

/* 4. The hover logic (no changes needed here) */
.forma-text-tooltip-container:hover .forma-text-tooltip {
  visibility: visible;
  opacity: 1;
}




        `}</style>
);

export default ToolbarButtonStyles;
