const DropdownStyles = () => (
  <style>{`
        .forma-text-dropdown {
          position: relative;
          display: inline-flex;
        }
        
        .forma-text-dropdown-toggle {
          display: inline-flex;
          align-items: center;
          gap: 0.25rem;
          padding: 0.375rem 0.75rem;
          font-size: 1rem;
          font-weight: 400;
          line-height: 1.5;
          color: #6c757d;
          background-color: transparent;
          border: 1px solid #6c757d;
          border-radius: 0.25rem;
          cursor: pointer;
          transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out;
        }

        .forma-text-dropdown-toggle:hover {
          color: #fff;
          background-color: #6c757d;
        }

        /* The little down arrow */
        .forma-text-dropdown-toggle::after {
          display: inline-block;
          margin-left: 0.255em;
          vertical-align: 0.255em;
          content: "";
          border-top: 0.3em solid;
          border-right: 0.3em solid transparent;
          border-bottom: 0;
          border-left: 0.3em solid transparent;
        }

        .forma-text-dropdown-menu {
          position: absolute;
          top: 100%;
          left: 0;
          z-index: 1000;
          display: none; /* Hidden by default */
          min-width: 10rem;
          padding: 0.5rem 0;
          margin-top: 0.125rem;
          font-size: 1rem;
          color: #212529;
          text-align: left;
          list-style: none;
          background-color: #fff;
          background-clip: padding-box;
          border: 1px solid rgba(0, 0, 0, 0.15);
          border-radius: 0.25rem;
          box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.175);
        }

        .forma-text-dropdown-menu--show {
          display: block; /* Shown when the 'show' class is added */
        }
      `}</style>
);
export default DropdownStyles;
