const FormaTextStyles = () => (
  <style>
    {`
.forma-text-editor-container {
  overflow: hidden !important;
}

.forma-text-editor-content:focus {
box-shadow: none;
}
.forma-text-modal .forma-text-editor-secondary {
background-color: #6c757d;
color: white;
font-size: 14px;
border-radius: 0px;
}
.forma-text-modal .forma-text-editor-primary {
background-color: #4285f4;
color: white;
font-size: 14px;
border-radius: 0px;
}
.forma-text-editor-content .forma-text-row {
position: relative;
margin: 1rem 0;
}
.forma-text-editor-content .forma-text-row:hover .forma-text-delete-column-btn {
display: flex;
}
.forma-text-delete-column-btn {
display: none;
position: absolute;
top: -10px;
right: -10px;
z-index: 10;
border-radius: 50%;
width: 24px;
height: 24px;
padding: 0;
align-items: center;
justify-content: center;
}
.forma-text-editor-content .forma-text-row > [class^="col-"] {
border: 1px dashed #ccc;
padding: 1rem;
min-height: 100px;
}
.forma-text-editor-content blockquote {
border-left: 4px solid #ccc;
margin-left: 0;
padding-left: 1rem;
color: #6c757d;
}
.forma-text-editor-content pre {
background-color: #f8f9fa;
border: 1px solid #dee2e6;
padding: 1rem;
border-radius: 0.25rem;
white-space: pre-wrap;
word-break: break-all;
}
.forma-text-color-swatch {
width: 24px;
height: 24px;
border-radius: 50%;
cursor: pointer;
border: 1px solid #ddd;
transition: transform 0.1s ease-in-out;
padding: 0;
}
.forma-text-color-swatch:hover {
transform: scale(1.1);
}
.forma-text-btn-group .forma-text-btn {
display: flex;
align-items: center;
justify-content: center;
}
.forma-text-image-wrapper {
position: relative;
display: inline-block;
}
.forma-text-image-wrapper.selected {
outline: 2px solid #0d6efd;
}
.forma-text-resize-handle {
position: absolute;
width: 12px;
height: 12px;
background-color: #0d6efd;
border: 2px solid #fff;
border-radius: 50%;
z-index: 10;
}
.forma-text-resize-handle.bottom-right {
bottom: -6px;
right: -6px;
cursor: nwse-resize;
}
.forma-text-image-toolbar,
.forma-text-table-toolbar {
  position: absolute;
  z-index: 10;
  display: flex;
  gap: 4px;
  padding: 4px;
  border-radius: 5px;
}



.forma-text-video-wrapper {
position: relative;
padding-bottom: 56.25%;
height: 0;
overflow: hidden;
max-width: 100%;
background: #000;
border-radius: 0.25rem;
margin: 1rem 0;
}
.forma-text-video-wrapper iframe {
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
}
.forma-text-video-modal-title {
font-size: 14px;
}
.forma-text-footer-text-area {
font-size: 18px;
font-weight: 600;
}
.forma-text-ai-preview-pane {
height: 400px;
overflow-y: auto;
border: 1px solid #dee2e6;
padding: 1rem;
border-radius: 0.25rem;
background-color: #fff;
}
.forma-text-ai-loading-popup {
position: fixed;
bottom: 20px;
right: 20px;
z-index: 1100;
width: 350px;
background-color: white;
border-radius: 0.5rem;
box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
padding: 1rem;
}
.forma-text-ai-loading-popup-minimized {
position: fixed;
bottom: 20px;
right: 20px;
z-index: 1100;
background-color: white;
border-radius: 0.5rem;
box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
padding: 0.5rem 1rem;
display: flex;
align-items: center;
}
`}
  </style>
);

export default FormaTextStyles;
