import { useState } from "react";
import PropTypes from "prop-types";
import { Table } from "lucide-react";
import BaseModal from "./BaseModal";
import TableStyles from "./styles/Table.styles";

const TableModal = ({ isOpen, onClose, onCreate }) => {
  const [tableData, setTableData] = useState({ rows: 3, cols: 3 });

  if (!isOpen) return null;

  return (
    <>
 <TableStyles/>
      <BaseModal
        onClose={onClose}
        title="Create Table"
        icon={<Table size={20} />}
      >
        <div className="forma-text-table-modal-body">
          <div className="forma-text-table-form-group">
            <label className="forma-text-table-label">Rows</label>
            <input
              type="number"
              className="forma-text-table-input"
              value={tableData.rows}
              min="1"
              onChange={(e) =>
                setTableData({
                  ...tableData,
                  rows: parseInt(e.target.value, 10) || 1,
                })
              }
            />
          </div>
          <div className="forma-text-table-form-group">
            <label className="forma-text-table-label">Columns</label>
            <input
              type="number"
              className="forma-text-table-input"
              value={tableData.cols}
              min="1"
              onChange={(e) =>
                setTableData({
                  ...tableData,
                  cols: parseInt(e.target.value, 10) || 1,
                })
              }
            />
          </div>
        </div>
        <div className="forma-text-table-modal-footer">
          <button
            type="button"
            className="forma-text-table-button forma-text-table-button--secondary"
            onClick={onClose}
          >
            Cancel
          </button>
          <button
            type="button"
            className="forma-text-table-button forma-text-table-button--primary"
            onClick={() => onCreate(tableData)}
          >
            Create
          </button>
        </div>
      </BaseModal>
    </>
  );
};

TableModal.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onCreate: PropTypes.func.isRequired,
};

export default TableModal;