import React from "react";
import PropTypes from "prop-types";

const FuturisticToolbarButton = React.memo(
  ({
    onClick,
    title,
    cmd,
    active,
    disabled = false,
    children,
    className = "",
  }) => (
    <>
      <style>
        {`
      
      

.futuristic-forma-text-tooltip {
  visibility: hidden;
  opacity: 0;
  width: max-content;
  background-color: #63b1ffff;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 6px 12px;
  font-size: 12px;
  font-weight: 500;
  position: absolute;
  z-index: 10;
  top: 130%; /* Changed from 'bottom' */
  left: 50%;
  transform: translateX(-50%);
  transition: opacity 0.2s ease-in-out;
}

.futuristic-forma-text-tooltip::after {
  content: "";
  position: absolute;
  bottom: 100%; 
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent #63b1ffff transparent;
}

.futuristic-forma-text-tooltip {
  visibility: visible;
  opacity: 1;
}



      
      `}
      </style>

      <button
        type="button"
        onClick={onClick}
        onMouseDown={(e) => e.preventDefault()}
        className={`futuristic-button ${
          active && active.has(cmd) ? "active" : ""
        } ${className}`}
        title={title}
        disabled={disabled}
      >
        <span className="button-content">{children}</span>
        <span className="futuristic-forma-text-tooltip">{title}</span>
      </button>
    </>
  )
);

FuturisticToolbarButton.propTypes = {
  onClick: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  cmd: PropTypes.string,
  active: PropTypes.object,
  disabled: PropTypes.bool,
  children: PropTypes.node.isRequired,
  className: PropTypes.string,
};

FuturisticToolbarButton.displayName = "FuturisticToolbarButton";

export default FuturisticToolbarButton;
