const AiLoadingPopupStyles = () => (
  <style>{`
        /* Base styles for the popup container */
        .forma-text-ai-popup, .forma-text-ai-popup-minimized {
          position: fixed;
          bottom: 20px;
          right: 20px;
          z-index: 1100;
          background-color: white;
          border-radius: 0.5rem;
          box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
        }

        /* Full-size popup */
        .forma-text-ai-popup {
          width: 350px;
          padding: 1rem;
        }
        .forma-text-ai-popup-header {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 1rem;
        }
        .forma-text-ai-popup-title {
          margin: 0;
          display: flex;
          align-items: center;
          gap: 0.5rem;
          font-size: 1rem; /* h6 default */
          font-weight: 500; /* h6 default */
        }
        .forma-text-ai-popup-body {
          display: flex;
          align-items: center;
        }
        .forma-text-popup-text-main {
          margin: 0;
          font-weight: 700;
        }
        .forma-text-popup-text-status {
          margin: 0;
          color: #6c757d;
          font-size: 0.875em;
        }
        .forma-text-icon--primary {
          color: #0d6efd;
        }
        
        /* Minimized popup */
        .forma-text-ai-popup-minimized {
          padding: 0.5rem 1rem;
          display: flex;
          align-items: center;
          gap: 0.5rem;
        }
        .forma-text-minimized-text {
            font-size: 0.875em;
        }
        
        /* Shared minimize/toggle button */
        .forma-text-ai-popup-btn {
          display: inline-flex;
          align-items: center;
          justify-content: center;
          padding: 0.25rem 0.5rem;
          font-size: 0.875rem;
          color: #212529;
          background-color: #f8f9fa;
          border: 1px solid #f8f9fa;
          border-radius: 0.25rem;
          cursor: pointer;
          transition: background-color 0.15s ease-in-out;
        }
        .forma-text-ai-popup-btn:hover {
            background-color: #e2e6ea;
        }
        .forma-text-ai-popup-btn--auto-margin {
            margin-left: auto;
        }

        /* Spinner animation and styles */
        @keyframes forma-text-spinner-border {
          to { transform: rotate(360deg); }
        }
        .forma-text-spinner {
          display: inline-block;
          vertical-align: text-bottom;
          border: 0.25em solid currentColor;
          border-right-color: transparent;
          border-radius: 50%;
          animation: forma-text-spinner-border .75s linear infinite;
          width: 2rem;
          height: 2rem;
          margin-right: 1rem;
        }
        .forma-text-spinner--sm {
          width: 1rem;
          height: 1rem;
          border-width: 0.2em;
          margin-right: 0;
        }
      `}</style>
);
export default AiLoadingPopupStyles;
