import React from "react";
import PropTypes from "prop-types";
import ToolbarButtonStyles from "./styles/ToolbarButton.styles";

/**
 * A reusable, memoized button component for the editor toolbar.
 */
const ToolbarButton = React.memo(
  ({ onClick, title, command, activeStyles, disabled = false, children }) => {
    const buttonClasses = [
      "forma-text-toolbar-button",
      activeStyles && activeStyles.has(command)
        ? "forma-text-toolbar-button--active"
        : "",
    ]
      .join(" ")
      .trim();

    return (
      <>
        <ToolbarButtonStyles />
        <div className="forma-text-tooltip-container">
          <button
            type="button"
            onClick={onClick}
            onMouseDown={(e) => e.preventDefault()}
            className={buttonClasses}
            disabled={disabled}
            aria-label={title}
          >
            {children}
          </button>
          <span className="forma-text-tooltip">{title}</span>
        </div>
      </>
    );
  }
);

ToolbarButton.propTypes = {
  onClick: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  command: PropTypes.string,
  activeStyles: PropTypes.object,
  disabled: PropTypes.bool,
  children: PropTypes.node.isRequired,
};

ToolbarButton.displayName = "FormaTextToolbarButton";

export default ToolbarButton;
