import { useState } from "react";
import PropTypes from "prop-types";
import { Sparkles } from "lucide-react";
import BaseModal from "../modal/BaseModal";
import AiSuggestionModalStyles from "./styles/AiSuggestionModal.styles";

const AiSuggestionModal = ({
  isOpen,
  onClose,
  onApply,
  originalContent,
  suggestion,
  highlightedDiff,
}) => {
  const [activeTab, setActiveTab] = useState("preview"); // 'preview' or 'diff'

  if (!isOpen) return null;

  // --- Class Name Helper ---
  const getTabButtonClasses = (tabName) => {
    let classes = "forma-text-ai-modal-tab-button";
    if (activeTab === tabName) {
      classes += " forma-text-ai-modal-tab-button--active";
    }
    return classes;
  };

  return (
    <>
      <AiSuggestionModalStyles />
      <BaseModal
        onClose={onClose}
        title="AI Content Suggestion"
        icon={<Sparkles size={20} />}
        size="1200px"
      >
        <div className="forma-text-ai-modal-body">
          <div className="forma-text-ai-modal-tab-container">
            <button
              className={getTabButtonClasses("preview")}
              onClick={() => setActiveTab("preview")}
            >
              Preview Changes
            </button>
            <button
              className={getTabButtonClasses("diff")}
              onClick={() => setActiveTab("diff")}
            >
              Highlight Changes
            </button>
          </div>

          {activeTab === "preview" && (
            <div className="forma-text-ai-modal-tab-content">
              <div className="forma-text-ai-modal-pane">
                <h6 className="forma-text-ai-modal-pane-title">
                  Original Content
                </h6>
                <div
                  className="forma-text-ai-modal-preview-pane"
                  dangerouslySetInnerHTML={{ __html: originalContent }}
                />
              </div>
              <div className="forma-text-ai-modal-pane">
                <h6 className="forma-text-ai-modal-pane-title forma-text-ai-modal-pane-title--suggestion">
                  AI Suggestion
                </h6>
                <div
                  className="forma-text-ai-modal-preview-pane"
                  dangerouslySetInnerHTML={{ __html: suggestion }}
                />
              </div>
            </div>
          )}

          {activeTab === "diff" && (
            <div>
              <h6 className="forma-text-ai-modal-pane-title forma-text-ai-modal-pane-title--diff">
                Highlighted Differences
              </h6>
              <div className="forma-text-ai-modal-diff-legend">
                <span className="forma-text-ai-modal-legend-added">
                  Added content
                </span>
                <span className="forma-text-ai-modal-legend-removed">
                  Removed content
                </span>
              </div>
              <div
                className="forma-text-ai-modal-preview-pane"
                dangerouslySetInnerHTML={{ __html: highlightedDiff }}
              />
            </div>
          )}
        </div>
        <div className="forma-text-ai-modal-footer">
          <button
            type="button"
            className="forma-text-ai-modal-button forma-text-ai-modal-button--secondary"
            onClick={onClose}
          >
            Cancel
          </button>
          <button
            type="button"
            className="forma-text-ai-modal-button forma-text-ai-modal-button--primary"
            onClick={onApply}
          >
            Apply Suggestion
          </button>
        </div>
      </BaseModal>
    </>
  );
};

AiSuggestionModal.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onApply: PropTypes.func.isRequired,
  originalContent: PropTypes.string,
  suggestion: PropTypes.string,
  highlightedDiff: PropTypes.string,
};

export default AiSuggestionModal;
