import PropTypes from "prop-types";
import { CodeXml, Maximize, Minimize } from "lucide-react";
import StatusbarButton from "../StatusbarButton";

/**
 * The status bar at the bottom of the editor.
 */
const ClassicStatusbar = ({
  wordCount,
  charCount,
  onToggleSource,
  isFullScreen,
  onToggleFullScreen,
}) => {
  // Define inline style objects to replace Bootstrap classes
  const statusbarStyle = {
    flexShrink: 0,
    padding: "0.5rem",
    borderTop: "1px solid #dee2e6",
    backgroundColor: "#f8f9fa",
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    fontSize: "0.875em",
    color: "#6c757d",
  };

  const footerTextStyle = {
    fontSize: "14px",
    fontWeight: 600,
  };

  const dividerStyle = {
    margin: "0 0.5rem",
  };

  const buttonGroupStyle = {
    display: "inline-flex",
    verticalAlign: "middle",
    gap: "4px", // Adds space between buttons
  };

  return (
    <>
      <div className="forma-text-classic-statusbar" style={statusbarStyle}>
        <div>
          <span
            className="forma-text-footer-text-areas"
            style={footerTextStyle}
          >
            Words: {wordCount}
          </span>
          <span style={dividerStyle}>|</span>
          <span className="forma-text-footer-text-area" style={footerTextStyle}>
            Characters: {charCount}
          </span>
        </div>
        <div className="forma-text-btn-group" style={buttonGroupStyle}>
          <StatusbarButton title="View HTML Source" onClick={onToggleSource}>
            <CodeXml size={16} />
          </StatusbarButton>

          <StatusbarButton
            title={isFullScreen ? "Exit Full Screen" : "Full Screen"}
            onClick={onToggleFullScreen}
          >
            {isFullScreen ? <Minimize size={16} /> : <Maximize size={16} />}
          </StatusbarButton>
        </div>
      </div>
    </>
  );
};

ClassicStatusbar.propTypes = {
  wordCount: PropTypes.number.isRequired,
  charCount: PropTypes.number.isRequired,
  onToggleSource: PropTypes.func.isRequired,
  showSource: PropTypes.bool.isRequired,
  onToggleFullScreen: PropTypes.func.isRequired,
  isFullScreen: PropTypes.bool.isRequired,
};

export default ClassicStatusbar;
