import PropTypes from "prop-types";
import { CodeXml, Maximize, Minimize } from "lucide-react";

const ModernStatusBar = ({
  wordCount,
  charCount,
  onToggleSource,
  isFullScreen,
  onToggleFullScreen,
  theme = "light", // Receive the theme prop, default to 'light'
}) => {
  // Define styles for both light and dark themes
  const themes = {
    light: {
      barBg: "#f8f9fa",
      barBorder: "#e0e0e0",
      barColor: "#5f6368",
      btnHoverBg: "#f1f3f4",
      btnHoverColor: "#202124",
    },
    dark: {
      barBg: "#2c2d2f",
      barBorder: "#3c4043",
      barColor: "#bdc1c6",
      btnHoverBg: "#3c4043",
      btnHoverColor: "#e8eaed",
    },
  };

  const currentTheme = themes[theme] || themes.light;

  // Define inline style objects
  const statusBarStyle = {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    padding: "6px 16px",

    borderTop: `1px solid ${currentTheme.barBorder}`,
    backgroundColor: currentTheme.barBg,
    color: currentTheme.barColor,
    transition:
      "background-color 0.3s ease, border-color 0.3s ease, color 0.3s ease",
  };

  const segmentStyle = {
    display: "flex",
    alignItems: "center",
    gap: "12px",
  };

  const dividerStyle = {
    opacity: 0.5,
  };

  const buttonStyle = {
    background: "none",
    border: "none",
    borderRadius: "6px",
    padding: "6px",
    cursor: "pointer",
    display: "inline-flex",
    alignItems: "center",
    justifyContent: "center",
    color: currentTheme.barColor, // Inherit color from the bar
    transition: "background-color 0.2s ease, color 0.2s ease",
  };

  return (
    <>
      <style>
        {`
          .forma-text-statusbar-button:hover {
            background-color: ${currentTheme.btnHoverBg};
            color: ${currentTheme.btnHoverColor};
          }

          .forma-text-statusbar-item{
          font-size: 14px;
          font-weight: 800;
          font-style: italic;
          }
        `}
      </style>

      <div className="forma-text-modern-statusbar" style={statusBarStyle}>
        <div className="forma-text-statusbar-segment" style={segmentStyle}>
          <span className="forma-text-statusbar-item">Words: {wordCount}</span>
          <span className="forma-text-statusbar-divider" style={dividerStyle}>
            |
          </span>
          <span className="forma-text-statusbar-item">
            Characters: {charCount}
          </span>
        </div>
        <div className="forma-text-statusbar-segment" style={segmentStyle}>
          <button
            className="forma-text-statusbar-button"
            title="View HTML Source"
            onClick={onToggleSource}
            style={buttonStyle}
          >
            <CodeXml size={18} />
          </button>
          <button
            className="forma-text-statusbar-button"
            title={isFullScreen ? "Exit Full Screen" : "Full Screen"}
            onClick={onToggleFullScreen}
            style={buttonStyle}
          >
            {isFullScreen ? <Minimize size={18} /> : <Maximize size={18} />}
          </button>
        </div>
      </div>
    </>
  );
};

ModernStatusBar.propTypes = {
  wordCount: PropTypes.number.isRequired,
  charCount: PropTypes.number.isRequired,
  onToggleSource: PropTypes.func.isRequired,
  onToggleFullScreen: PropTypes.func.isRequired,
  isFullScreen: PropTypes.bool.isRequired,
  theme: PropTypes.string,
};

export default ModernStatusBar;
