import PropTypes from "prop-types";
import BaseModalStyles from "./styles/BaseModal.styles";

/**
 * A generic, reusable modal component using component-based styles.
 */
const BaseModal = ({ children, onClose, title, icon, size = "500px" }) => {
  // This style object is only for the dynamic width
  const dialogDynamicStyle = {
    "--forma-text-modal-width": size,
  };

  return (
    <>
      <BaseModalStyles />
      <div className="forma-text-modal-overlay" onClick={onClose}>
        <div
          className="forma-text-modal-dialog"
          style={dialogDynamicStyle}
          onClick={(e) => e.stopPropagation()}
        >
          <div className="forma-text-modal-header">
            <h5 className="forma-text-modal-title">
              {icon}
              {title}
            </h5>
            <button className="forma-text-modal-close-button" onClick={onClose}>
              &times;
            </button>
          </div>
          <div className="forma-text-modal-content">{children}</div>
        </div>
      </div>
    </>
  );
};

BaseModal.propTypes = {
  children: PropTypes.node.isRequired,
  onClose: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  icon: PropTypes.node,
  size: PropTypes.string,
};

export default BaseModal;
