import PropTypes from "prop-types";
import { Sparkles, Minimize, ChevronsDownUp } from "lucide-react";
import AiLoadingPopupStyles from "./styles/AiLoadingPopup.styles";

/**
 * A loading popup for the AI generation process.
 */
const AiLoadingPopup = ({ status, isMinimized, onMinimizeToggle }) => {
  // The component returns a fragment to hold both the styles and the JSX.
  return (
    <>
      <AiLoadingPopupStyles />
      {isMinimized ? (
        <div className="forma-text-ai-popup-minimized">
          <div
            className="forma-text-spinner forma-text-spinner--sm forma-text-icon--primary"
            role="status"
          ></div>
          <span className="forma-text-minimized-text">AI is working...</span>
          <button
            onClick={onMinimizeToggle}
            className="forma-text-ai-popup-btn forma-text-ai-popup-btn--auto-margin"
          >
            <ChevronsDownUp size={14} />
          </button>
        </div>
      ) : (
        <div className="forma-text-ai-popup">
          <div className="forma-text-ai-popup-header">
            <h6 className="forma-text-ai-popup-title">
              <Sparkles size={18} className="forma-text-icon--primary" />
              AI Assistant
            </h6>
            <button
              onClick={onMinimizeToggle}
              className="forma-text-ai-popup-btn"
            >
              <Minimize size={14} />
            </button>
          </div>
          <div className="forma-text-ai-popup-body">
            <div
              className="forma-text-spinner forma-text-icon--primary"
              role="status"
            ></div>
            <div>
              <p className="forma-text-popup-text-main">Working on it...</p>
              <p className="forma-text-popup-text-status">{status}</p>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

AiLoadingPopup.propTypes = {
  status: PropTypes.string.isRequired,
  isMinimized: PropTypes.bool.isRequired,
  onMinimizeToggle: PropTypes.func.isRequired,
};

export default AiLoadingPopup;
