const ClassicToolbarStyles = () => (
  <style>{`
        .forma-text-classic-toolbar {
          padding: 0.5rem;
          border-bottom: 1px solid #dee2e6;
          display: flex;
          flex-wrap: wrap;
          align-items: center;
          gap: 0.5rem;
        }
        .forma-text-btn-group {
          display: flex;
          align-items: center;
          gap: 2px;
        }
        .forma-text-dropdown-item {
          display: block;
          width: 100%;
          padding: 0.5rem 1rem;
          font-size: 14px;
          text-align: left;
          color: #212529;
          background-color: transparent;
          border: none;
          cursor: pointer;
          transition: background-color 0.15s ease-in-out;
        }
        .forma-text-dropdown-item:hover {
          background-color: #f8f9fa;
        }
        .forma-text-dropdown-item--danger {
          color: #dc3545;
          margin-top: 0.5rem;
          border-top: 1px solid #dc3545;
        }
        .forma-text-font-icon {
            font-weight: bold;
            font-size: 12px;
        }
        .forma-text-row {
            display: flex;
            gap: 1rem;
            position: relative;
            margin: 1rem 0;
        }
        .forma-text-row:hover .forma-text-delete-btn {
            display: flex;
        }
        .forma-text-col {
            border: 1px dashed #ccc;
            padding: 1rem;
            min-height: 100px;
        }
        .forma-text-col--1 { flex: 1; }
        .forma-text-col--2 { flex: 2; }

        .forma-text-delete-btn {
            display: none;
            position: absolute;
            top: -10px;
            right: -10px;
            z-index: 10;
            background-color: #dc3545;
            color: white;
            border: none;
            border-radius: 50%;
            width: 24px;
            height: 24px;
            align-items: center;
            justify-content: center;
            cursor: pointer;
        }

        @keyframes forma-text-spinner-border {
          to { transform: rotate(360deg); }
        }
        .forma-text-spinner {
          display: inline-block;
          width: 1rem;
          height: 1rem;
          vertical-align: text-bottom;
          border: 0.2em solid currentColor;
          border-right-color: transparent;
          border-radius: 50%;
          animation: forma-text-spinner-border .75s linear infinite;
        }
        .forma-text-hidden {
            display: none;
        }
      `}</style>
);

export default ClassicToolbarStyles;
