import { useState, useRef, useCallback, useEffect } from "react";
import PropTypes from "prop-types";
import mammoth from "mammoth";
import {
  diff_match_patch,
  DIFF_DELETE,
  DIFF_INSERT,
  DIFF_EQUAL,
} from "diff-match-patch";

import Toolbar from "./components/Toolbar/Toolbar";
import Editor from "./components/Editor";
import LinkModal from "./modal/LinkModal";
import VideoModal from "./modal/VideoModal";
import TableModal from "./modal/TableModal";
import AiLoadingPopup from "./ai/AiLoadingPopup";
import AiSuggestionModal from "./ai/AiSuggestionModal";
import {
  AlignCenterVertical,
  AlignEndVertical,
  AlignStartVertical,
  Trash2,
} from "lucide-react";
import StatusBar from "./components/statusBar/StatusBar";
import FormaTextStyles from "./FormaText.styles";

/**
 * The main parent component for the HTML Editor.
 * It manages state and orchestrates all the child components.
 */
export default function FormaText({
  content = "",
  setContent = "",
  geminiApiKey = "",
  parentEditorClass = "",
  toolBarClass = "",
  textContentClass = "",
  theme = "",
}) {
  const editorRef = useRef(null);
  const savedRange = useRef(null);
  const isInternalUpdate = useRef(false);
  const [activeStyles, setActiveStyles] = useState(new Set());
  const [wordCount, setWordCount] = useState({ words: 0, chars: 0 });
  const [showSource, setShowSource] = useState(false);
  const [isFullScreen, setIsFullScreen] = useState(false);
  const [isLinkModalOpen, setIsLinkModalOpen] = useState(false);
  const [linkModalData, setLinkModalData] = useState({ text: "", href: "" });
  const [editingLink, setEditingLink] = useState(null);
  const [isVideoModalOpen, setIsVideoModalOpen] = useState(false);
  const [isTableModalOpen, setIsTableModalOpen] = useState(false);
  const [isAiModalOpen, setIsAiModalOpen] = useState(false);
  const [aiSuggestion, setAiSuggestion] = useState("");
  const [highlightedDiff, setHighlightedDiff] = useState("");
  const [isGenerating, setIsGenerating] = useState(false);
  const [aiLoadingStatus, setAiLoadingStatus] = useState("");
  const [isAiLoadingMinimized, setIsAiLoadingMinimized] = useState(false);
  const [selectedImage, setSelectedImage] = useState(null);
  const [selectedTable, setSelectedTable] = useState(null);
  const [imageResizeCounter, setImageResizeCounter] = useState(0);

  const saveSelection = useCallback(() => {
    const selection = window.getSelection();
    if (selection.rangeCount > 0) {
      const range = selection.getRangeAt(0);
      if (
        editorRef.current &&
        editorRef.current.contains(range.commonAncestorContainer)
      ) {
        savedRange.current = range;
      }
    }
  }, []);

  const restoreSelection = useCallback(() => {
    if (savedRange.current) {
      const selection = window.getSelection();
      selection.removeAllRanges();
      selection.addRange(savedRange.current);
    }
  }, []);

  const updateContent = useCallback(() => {
    if (editorRef.current) {
      isInternalUpdate.current = true;
      setContent(editorRef.current.innerHTML);
    }
  }, []);

  const handleEditorContentChange = useCallback(
    (e) => {
      // This handles both the contentEditable div's input and the textarea's change
      const newContent = showSource
        ? e.target.value
        : e.currentTarget.innerHTML;
      isInternalUpdate.current = true;
      setContent(newContent);
    },
    [showSource]
  );

  const updateToolbarState = useCallback(() => {
    const newActiveStyles = new Set();
    const commands = [
      "bold",
      "italic",
      "underline",
      "strikeThrough",
      "justifyLeft",
      "justifyCenter",
      "justifyRight",
      "insertOrderedList",
      "insertUnorderedList",
      "superscript",
      "subscript",
    ];
    commands.forEach((command) => {
      if (document.queryCommandState(command)) {
        newActiveStyles.add(command);
      }
    });
    setActiveStyles(newActiveStyles);
  }, []);

  const applyStyle = useCallback(
    (style, value = null) => {
      editorRef.current?.focus();
      restoreSelection();
      document.execCommand(style, false, value);
      updateContent();
      updateToolbarState();
    },
    [restoreSelection, updateContent, updateToolbarState]
  );
  const applyLineHeight = useCallback(
    (lineHeight) => {
      editorRef.current?.focus();
      restoreSelection();

      const selection = window.getSelection();
      if (!selection.rangeCount) return;

      // Helper function to find the closest block-level parent of a node
      const getContainingBlock = (node) => {
        // Start from the node itself if it's an element, or its parent if it's text
        let element =
          node.nodeType === Node.ELEMENT_NODE ? node : node.parentNode;
        while (element && element !== editorRef.current) {
          // Check if the element's display style is block-like
          if (
            window
              .getComputedStyle(element)
              .display.match(/block|table|list-item/)
          ) {
            return element;
          }
          element = element.parentNode;
        }
        return null;
      };

      const range = selection.getRangeAt(0);
      const startBlock = getContainingBlock(range.startContainer);
      const endBlock = getContainingBlock(range.endContainer);

      if (startBlock) {
        let currentBlock = startBlock;

        // Loop from the first selected block to the last
        while (currentBlock) {
          currentBlock.style.lineHeight = lineHeight;

          // Stop if we've processed the last block in the selection
          if (currentBlock === endBlock) break;

          // Move to the next block-level element
          currentBlock = currentBlock.nextElementSibling;
        }
      }

      updateContent();
    },
    [restoreSelection, updateContent]
  );

  useEffect(() => {
    if (isInternalUpdate.current) {
      isInternalUpdate.current = false;
      return;
    }
    if (
      editorRef.current &&
      !showSource &&
      content !== editorRef.current.innerHTML
    ) {
      editorRef.current.innerHTML = content || "";
    }
  }, [content, showSource]);

  useEffect(() => {
    const tempDiv = document.createElement("div");
    tempDiv.innerHTML = content || "";
    const text = tempDiv.textContent || "";
    const words = text.trim().split(/\s+/).filter(Boolean).length;
    setWordCount({ words, chars: text.length });
  }, [content]);

  const handleSelectionChange = useCallback(() => {
    const selection = window.getSelection();
    if (selection.rangeCount > 0) {
      const range = selection.getRangeAt(0);
      if (
        editorRef.current &&
        editorRef.current.contains(range.commonAncestorContainer)
      ) {
        saveSelection();
        updateToolbarState();
      }
    }
  }, [saveSelection, updateToolbarState]);

  useEffect(() => {
    document.addEventListener("selectionchange", handleSelectionChange);
    return () =>
      document.removeEventListener("selectionchange", handleSelectionChange);
  }, [handleSelectionChange]);

  const handleKeyDown = useCallback(
    (e) => {
      if (e.ctrlKey || e.metaKey) {
        const keyMap = {
          b: "bold",
          i: "italic",
          u: "underline",
          k: () => handleOpenLinkModal(),
          z: "undo",
          y: "redo",
        };
        if (keyMap[e.key]) {
          e.preventDefault();
          typeof keyMap[e.key] === "function"
            ? keyMap[e.key]()
            : applyStyle(keyMap[e.key]);
        }
      }
    },
    [applyStyle]
  ); // --- Modal & File Handlers ---

  const handleOpenLinkModal = useCallback(() => {
    saveSelection();
    const selection = window.getSelection();
    const parentElement = selection.anchorNode?.parentElement.closest("a");
    if (parentElement?.tagName === "A") {
      setLinkModalData({
        text: parentElement.textContent,
        href: parentElement.href,
      });
      setEditingLink(parentElement);
    } else {
      setLinkModalData({ text: selection.toString(), href: "" });
      setEditingLink(null);
    }
    setIsLinkModalOpen(true);
  }, [saveSelection]);

  const handleApplyLink = useCallback(
    (data) => {
      setIsLinkModalOpen(false);
      restoreSelection();
      editorRef.current.focus();
      if (editingLink) {
        editingLink.href = data.href;
        editingLink.textContent = data.text;
      } else {
        const link = `<a href="${data.href}" target="_blank" rel="noopener noreferrer">${data.text}</a>`;
        applyStyle("insertHTML", link);
      }
      setEditingLink(null);
      updateContent();
    },
    [editingLink, restoreSelection, applyStyle, updateContent]
  );

  const handleImageUpload = useCallback(
    (event) => {
      const file = event.target.files[0];
      if (!file) return;
      const reader = new FileReader();
      reader.onload = (e) => {
        const imgHtml = `<span class="forma-text-image-wrapper" style="display: inline-block;"><img src="${e.target.result}" class="forma-text-img-fluid" style="max-width: 100%; height: auto; border-radius: 0.25rem; width: 250px;" /></span>`;
        applyStyle("insertHTML", imgHtml);
      };
      reader.readAsDataURL(file);
      event.target.value = "";
    },
    [applyStyle]
  );

  const handleDocxUpload = useCallback((event) => {
    const file = event.target.files[0];
    if (!file) return;
    const reader = new FileReader();
    reader.onload = (e) => {
      mammoth
        .convertToHtml({ arrayBuffer: e.target.result })
        .then((result) => setContent(result.value))
        .catch((error) =>
          console.error("Error converting .docx to HTML:", error)
        );
    };
    reader.readAsArrayBuffer(file);
    event.target.value = "";
  }, []);

  const getEmbedUrl = (url) => {
    let embedUrl = "";
    let videoId;
    if (url.includes("youtube.com/watch?v=")) {
      videoId = url.split("v=")[1].split("&")[0];
      embedUrl = `https://www.youtube.com/embed/${videoId}`;
    } else if (url.includes("youtu.be/")) {
      videoId = url.split("youtu.be/")[1].split("?")[0];
      embedUrl = `https://www.youtube.com/embed/${videoId}`;
    } else if (url.includes("vimeo.com/")) {
      videoId = url.split("vimeo.com/")[1].split("?")[0];
      embedUrl = `https://player.vimeo.com/video/${videoId}`;
    }
    return embedUrl;
  };

  const handleInsertVideo = useCallback(
    (videoUrl) => {
      setIsVideoModalOpen(false);
      if (!videoUrl) return;
      const embedUrl = getEmbedUrl(videoUrl);
      if (!embedUrl) {
        console.warn(
          "Invalid video URL. Please use a valid YouTube or Vimeo link."
        );
        return;
      }
      restoreSelection();
      editorRef.current.focus();
      const videoHtml = `<div class="forma-text-video-wrapper"><iframe src="${embedUrl}" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe></div><p><br></p>`;
      applyStyle("insertHTML", videoHtml);
      updateContent();
    },
    [restoreSelection, applyStyle, updateContent]
  );

  const handleCreateTable = useCallback(
    (tableData) => {
      setIsTableModalOpen(false);
      let tableHTML =
        '<table class="forma-text-table" style="width: 100%; border-collapse: collapse; border: 1px solid #dee2e6;"><tbody>';
      for (let i = 0; i < tableData.rows; i++) {
        tableHTML += "<tr>";
        for (let j = 0; j < tableData.cols; j++)
          tableHTML +=
            '<td style="border: 1px solid #dee2e6; padding: 0.75rem;"><br></td>';
        tableHTML += "</tr>";
      }
      tableHTML += "</tbody></table><p><br></p>";
      applyStyle("insertHTML", tableHTML);
    },
    [applyStyle]
  ); // --- AI Logic ---

  const generateDiffHtml = (original, suggestion) => {
    const dmp = new diff_match_patch();
    const diff = dmp.diff_main(original, suggestion);
    dmp.diff_cleanupSemantic(diff);
    let html = "";
    for (const [op, data] of diff) {
      const text = data
        .replace(/&/g, "&")
        .replace(/</g, "<")
        .replace(/>/g, ">")
        .replace(/\n/g, "¶<br>");
      switch (op) {
        case DIFF_INSERT:
          html += `<ins style="background-color: #d4edda; text-decoration: none;">${text}</ins>`;
          break;
        case DIFF_DELETE:
          html += `<del style="background-color: #f8d7da; text-decoration: none;">${text}</del>`;
          break;
        case DIFF_EQUAL:
          html += `<span>${text}</span>`;
          break;
      }
    }
    return html;
  };

  const handleAiRefactor = async () => {
    if (!content || content === "" || isGenerating) return;

    setIsGenerating(true);
    setAiSuggestion("");
    setIsAiLoadingMinimized(false);

    const loadingMessages = [
      "Analyzing...",
      "Generating suggestions...",
      "Optimizing flow...",
      "Finalizing...",
    ];
    let messageIndex = 0;
    setAiLoadingStatus(loadingMessages[messageIndex]);
    const interval = setInterval(() => {
      messageIndex = (messageIndex + 1) % loadingMessages.length;
      setAiLoadingStatus(loadingMessages[messageIndex]);
    }, 2000);

    const prompt = `
<TASK>
You are a world-class content editor and a senior SEO strategist with elite proficiency in **English, Japanese, Hindi, French, Spanish, Sinhalese,Arabic and Chinese **. Your task is to reformat, stylish, and enhance the provided HTML content.
</TASK>

<LANGUAGE_PROTOCOL>
1. **Identify the Language:** Your first step is to determine if the content's primary language is one of the following:**English, Japanese, Hindi, French, Spanish, Sinhalese,Arabic and Chinese **.
  * **CRITICAL CLARIFICATION:** To do this, analyze **only the human-readable text**. You **must ignore all HTML tags** (e.g., \`<p>\`, \`<div>\`) during this language identification step.
2. **Lock the Language:** All subsequent tasks **must** be performed exclusively in the single language you have identified.
3. **No Translation:** You **must not** translate the content.
</LANGUAGE_PROTOCOL>

<INSTRUCTIONS>
1. **IMPROVE READABILITY & PROFESSIONALISM (in the identified language):**
  * Correct all grammar, spelling, and punctuation errors.
  * **Style tags appropriately using inline CSS. Ensure a clear visual hierarchy (e.g., "<h1 style="font-size: 2em;">", "<h2 style="font-size: 1.5em;">", "<p style="font-size: 1em;">").**
  * Add proper spaces and line heigh and font height for text
  * Maintain a professional and authoritative yet accessible tone.

2. **ENHANCE SEO & STRUCTURE (in the identified language):**
  * Ensure there is one single, compelling \`<h1>\` tag that captures the main topic.
  * Structure the content logically using \`<h2>\` for main sections and \`<h3>\` for sub-sections.
  * Where appropriate, convert lists or series of items into bulleted (\`<ul>\`) or numbered (\`<ol>\`) lists for better scannability.
  * Use \`<strong>\` for important keywords or phrases instead of \`<b>\`. Use \`<em>\` for emphasis instead of \`<i>\`.

3. **CORE CONTENT REQUIREMENTS:**
  * Preserve the original core message, facts, and meaning.
  * Use **inline CSS** for any styling (e.g., \`<p style="color: #333;">\`).
</INSTRUCTIONS>

<ABSOLUTE_OUTPUT_FORMAT>
-  **CRITICAL:** Your entire response must be **RAW HTML code ONLY**.
-  **DO NOT** under any circumstances wrap your response in markdown fences like \`\`\`html or \`\`\`.
-  **DO NOT** include any commentary, notes, or explanations before or after the code.
-  **DO NOT** include \`<html>\`, \`<head>\`, \`<body>\`, \`<title>\`, \`<meta>\`, or \`<script>\` tags.
-  Your response must start directly with the first HTML tag (e.g., \`<h1>\`) and end with the last closing tag.
</ABSOLUTE_OUTPUT_FORMAT>

<CONTENT_TO_IMPROVE>
${content}
</CONTENT_TO_IMPROVE>
`;
    try {
      const payload = {
        contents: [{ role: "user", parts: [{ text: prompt }] }],
      };
      const apiUrl = `https://generativelanguage.googleapis.com/v1beta/models/gemini-2.0-flash:generateContent?key=${geminiApiKey}`;
      const response = await fetch(apiUrl, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });
      if (!response.ok) throw new Error(`API call failed: ${response.status}`);

      const result = await response.json();
      const improvedHtml = result.candidates?.[0]?.content?.parts?.[0]?.text;

      if (improvedHtml) {
        setAiSuggestion(improvedHtml);
        setHighlightedDiff(generateDiffHtml(content, improvedHtml));
        setIsAiModalOpen(true);
      } else {
        throw new Error("Invalid AI response format");
      }
    } catch (error) {
      console.error("Error calling Gemini API:", error);
      setAiSuggestion(`<p>Sorry, an error occurred. ${error.message}</p>`);
      setHighlightedDiff(`<p>Error: ${error.message}</p>`);
      setIsAiModalOpen(true);
    } finally {
      clearInterval(interval);
      setIsGenerating(false);
      setAiLoadingStatus("");
    }
  };

  const handleApplyAiSuggestion = () => {
    setContent(aiSuggestion);
    setIsAiModalOpen(false);
    setAiSuggestion("");
    setHighlightedDiff("");
  }; // --- Image/Table Selection & Manipulation ---

  useEffect(() => {
    const editor = editorRef.current;
    if (!editor) return;

    const handleEditorClick = (e) => {
      // Column deletion
      if (e.target.closest(".forma-text-delete-column-btn")) {
        e.target.closest(".forma-text-row").remove();
        updateContent();
        return;
      } // Element selection

      const img = e.target.closest("img");
      const table = e.target.closest("table");

      setSelectedImage(img);
      setSelectedTable(table);

      if (img) setSelectedTable(null);
      if (table) setSelectedImage(null);
    };

    editor.addEventListener("click", handleEditorClick);
    return () => editor.removeEventListener("click", handleEditorClick);
  }, [updateContent]);

  const alignImage = (wrapper, alignment) => {
    if (!wrapper) return; // Reset styles

    wrapper.style.display = "inline-block";
    wrapper.style.margin = "0";
    wrapper.style.float = "none";

    if (alignment === "center") {
      wrapper.style.display = "block";
      wrapper.style.margin = "0 auto";
    } else if (alignment === "left") {
      wrapper.style.float = "left";
      wrapper.style.marginRight = "1rem";
      wrapper.style.marginBottom = "1rem";
    } else if (alignment === "right") {
      wrapper.style.float = "right";
      wrapper.style.marginLeft = "1rem";
      wrapper.style.marginBottom = "1rem";
    }
    updateContent();
  };

  return (
    <>
      <FormaTextStyles />
      <div
        className={`forma-text-editor-container ${parentEditorClass}`}
        style={{
          display: "flex",
          flexDirection: "column",
          border: "1px solid #dee2e6",
          borderRadius: "0.375rem",
          boxShadow: "0 0.125rem 0.25rem rgba(0, 0, 0, 0.075)",
          position: isFullScreen ? "fixed" : "relative",
          top: isFullScreen ? 0 : "auto",
          left: isFullScreen ? 0 : "auto",
          width: isFullScreen ? "100%" : "auto",
          height: isFullScreen ? "100%" : "auto",
          minHeight: isFullScreen ? "100%" : "60vh",
          maxHeight: isFullScreen ? "100%" : "95vh",
          backgroundColor: isFullScreen ? "white" : "transparent",
          zIndex: isFullScreen ? 1050 : "auto",
          overflowX: "hidden",
        }}
      >
        <div
          className={`forma-text-toolbar-container ${toolBarClass}`}
          style={{
            flexShrink: 0,
            borderBottom: "1px solid #dee2e6",
          }}
        >
          <Toolbar
            theme={theme}
            applyStyle={applyStyle}
            applyLineHeight={applyLineHeight}
            activeStyles={activeStyles}
            onOpenLinkModal={handleOpenLinkModal}
            onImageUpload={handleImageUpload}
            onDocxUpload={handleDocxUpload}
            onVideoModalOpen={() => {
              saveSelection();
              setIsVideoModalOpen(true);
            }}
            onTableModalOpen={() => {
              saveSelection();
              setIsTableModalOpen(true);
            }}
            onAiRefactor={handleAiRefactor}
            content={content}
            isGenerating={isGenerating}
            isAiLoadingMinimized={isAiLoadingMinimized}
          />
        </div>
        <div
          className="forma-text-editor-area"
          style={{
            flexGrow: 1,
            overflow: "auto",
            position: "relative",
            paddingTop: "1rem",
          }}
        >
          <Editor
            editorRef={editorRef}
            content={content}
            showSource={showSource}
            onContentChange={handleEditorContentChange}
            onKeyDown={handleKeyDown}
            textContentClass={textContentClass}
          />
          {selectedImage &&
            !showSource &&
            (() => {
              const editorRect = editorRef.current.getBoundingClientRect();
              const imageRect = selectedImage.getBoundingClientRect();
              const wrapper = selectedImage.parentElement;
              return (
                <div
                  className="forma-text-image-wrapper forma-text-selected"
                  style={{
                    position: "absolute",
                    top:
                      imageRect.top -
                      editorRect.top +
                      editorRef.current.scrollTop,
                    left:
                      imageRect.left -
                      editorRect.left +
                      editorRef.current.scrollLeft,
                    width: imageRect.width,
                    height: imageRect.height,
                    pointerEvents: "none",
                  }}
                >
                  <div
                    className="forma-text-image-toolbar"
                    style={{ pointerEvents: "auto" }}
                  >
                    <button
                      className="forma-text-btn"
                      style={{
                        backgroundColor: "#212529",
                        color: "white",
                        border: "none",
                        padding: "0.25rem 0.5rem",
                        fontSize: "0.875rem",
                        borderRadius: "0.25rem",
                      }}
                      onClick={() => alignImage(wrapper, "left")}
                    >
                      <AlignStartVertical size={14} />
                    </button>
                    <button
                      className="forma-text-btn"
                      style={{
                        backgroundColor: "#212529",
                        color: "white",
                        border: "none",
                        padding: "0.25rem 0.5rem",
                        fontSize: "0.875rem",
                        borderRadius: "0.25rem",
                      }}
                      onClick={() => alignImage(wrapper, "center")}
                    >
                      <AlignCenterVertical size={14} />
                    </button>
                    <button
                      className="forma-text-btn"
                      style={{
                        backgroundColor: "#212529",
                        color: "white",
                        border: "none",
                        padding: "0.25rem 0.5rem",
                        fontSize: "0.875rem",
                        borderRadius: "0.25rem",
                      }}
                      onClick={() => alignImage(wrapper, "right")}
                    >
                      <AlignEndVertical size={14} />
                    </button>
                    <button
                      className="forma-text-btn"
                      style={{
                        backgroundColor: "#dc3545",
                        color: "white",
                        border: "none",
                        padding: "0.25rem 0.5rem",
                        fontSize: "0.875rem",
                        borderRadius: "0.25rem",
                      }}
                      onClick={() => {
                        wrapper.remove();
                        updateContent();
                        setSelectedImage(null);
                      }}
                    >
                      <Trash2 size={14} />
                    </button>
                  </div>
                  <div
                    className="forma-text-resize-handle bottom-right"
                    style={{ pointerEvents: "auto" }}
                    onMouseDown={(e) => {
                      e.preventDefault();
                      const startX = e.clientX;
                      const startWidth = selectedImage.offsetWidth;
                      const doDrag = (moveEvent) => {
                        const newWidth =
                          startWidth + (moveEvent.clientX - startX);
                        if (newWidth > 20) {
                          selectedImage.style.width = `${newWidth}px`;
                          selectedImage.style.height = "auto";
                          setImageResizeCounter((c) => c + 1); // Force re-render
                        }
                      };
                      const stopDrag = () => {
                        document.removeEventListener("mousemove", doDrag);
                        document.removeEventListener("mouseup", stopDrag);
                        updateContent();
                      };
                      document.addEventListener("mousemove", doDrag);
                      document.addEventListener("mouseup", stopDrag);
                    }}
                  ></div>
                </div>
              );
            })()}
          {selectedTable &&
            !showSource &&
            (() => {
              const editorRect = editorRef.current.getBoundingClientRect();
              const tableRect = selectedTable.getBoundingClientRect();
              return (
                <div
                  className="forma-text-table-toolbar"
                  style={{
                    position: "absolute",
                    // Position the button slightly above the table's top edge
                    top: `${
                      tableRect.top -
                      editorRect.top +
                      editorRef.current.scrollTop -
                      15
                    }px`,
                    // Position the button's left edge at the table's right edge
                    left: `${
                      tableRect.left -
                      editorRect.left +
                      editorRef.current.scrollLeft +
                      tableRect.width
                    }px`,
                    // Then, pull the button back to the left by its own width to align it perfectly
                    transform: "translateX(-100%)",
                    pointerEvents: "auto",
                  }}
                >
                  <button
                    className="forma-text-btn"
                    style={{
                      backgroundColor: "#dc3545",
                      color: "white",
                      border: "none",
                      padding: "0.4rem",
                      fontSize: "0.875rem",
                      borderRadius: "50%",
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                      margin: "0.25rem",
                    }}
                    onClick={() => {
                      selectedTable.remove();
                      updateContent();
                      setSelectedTable(null);
                    }}
                  >
                    <Trash2 size={14} />
                  </button>
                </div>
              );
            })()}
        </div>
        <StatusBar
          theme={theme}
          wordCount={wordCount.words}
          charCount={wordCount.chars}
          onToggleSource={() => setShowSource(!showSource)}
          showSource={showSource}
          onToggleFullScreen={() => setIsFullScreen(!isFullScreen)}
          isFullScreen={isFullScreen}
        />
        {/* Modals and Popups */}
        {isGenerating && (
          <AiLoadingPopup
            status={aiLoadingStatus}
            isMinimized={isAiLoadingMinimized}
            onMinimizeToggle={() =>
              setIsAiLoadingMinimized(!isAiLoadingMinimized)
            }
          />
        )}
        <LinkModal
          isOpen={isLinkModalOpen}
          onClose={() => setIsLinkModalOpen(false)}
          onApply={handleApplyLink}
          initialData={linkModalData}
          isEditing={!!editingLink}
        />
        <VideoModal
          isOpen={isVideoModalOpen}
          onClose={() => setIsVideoModalOpen(false)}
          onInsert={handleInsertVideo}
        />
        <TableModal
          isOpen={isTableModalOpen}
          onClose={() => setIsTableModalOpen(false)}
          onCreate={handleCreateTable}
        />
        <AiSuggestionModal
          isOpen={isAiModalOpen}
          onClose={() => setIsAiModalOpen(false)}
          onApply={handleApplyAiSuggestion}
          originalContent={content}
          suggestion={aiSuggestion}
          highlightedDiff={highlightedDiff}
        />
      </div>
    </>
  );
}

FormaText.propTypes = {
  content: PropTypes.string,
  setContent: PropTypes.func,
  geminiApiKey: PropTypes.string,
  parentEditorClass: PropTypes.string,
  toolBarClass: PropTypes.string,
  textContentClass: PropTypes.string,
  theme: PropTypes.string,
};
