import PropTypes from "prop-types";
import EditorStyles from "./Editor.styles";

/**
 * The core contentEditable editor area.
 */
const Editor = ({
  editorRef,
  content,
  showSource,
  onContentChange,
  onKeyDown,
  textContentClass,
}) => {
  return (
    <>
      <EditorStyles />
      {showSource ? (
        <textarea
          value={content || ""}
          onChange={onContentChange}
          className="forma-text-source-view"
        />
      ) : (
        <div
          ref={editorRef}
          contentEditable
          suppressContentEditableWarning
          className={`forma-text-editor-content ${textContentClass}`}
          onInput={onContentChange}
          onKeyDown={onKeyDown}
        />
      )}
    </>
  );
};

Editor.propTypes = {
  editorRef: PropTypes.object.isRequired,
  content: PropTypes.string,
  showSource: PropTypes.bool.isRequired,
  onContentChange: PropTypes.func.isRequired,
  onKeyDown: PropTypes.func.isRequired,
  textContentClass: PropTypes.string,
};

export default Editor;
