import { useEffect, useRef, useState } from "react";
import PropTypes from "prop-types";
import {
  Bold,
  Italic,
  Underline,
  Strikethrough,
  Code,
  Link,
  Image,
  List,
  ListOrdered,
  Quote,
  AlignLeft,
  AlignCenter,
  AlignRight,
  Indent,
  Outdent,
  Table,
  Pilcrow,
  Eraser,
  Undo,
  Redo,
  Columns,
  Minus,
  Type,
  ArrowDownUp,
  FileUp,
  Superscript,
  Subscript,
  Video,
  Sparkles,
  GripVertical,
} from "lucide-react";
import FuturisticToolbarButton from "./components/FuturisticToolbarButton";
import FuturisticDropdown from "./components/FuturisticDropdown";
import FuturisticColorPicker from "./components/FuturisticColorPicker";
import FuturisticEmojiPicker from "./components/FuturisticEmojiPicker";
import FuturisticStyle from "./Futuristic.style";

// --- Constants (Unchanged) ---
const FONT_FACES = ["Arial", "Georgia", "Verdana", "Courier New"];
const HEADINGS = { H1: "<h1>", H2: "<h2>", H3: "<h3>", Paragraph: "<p>" };
const LINE_HEIGHTS = { Single: "1", 1.5: "1.5", Double: "2" };
const FONT_SIZES = { Small: "2", Normal: "3", Large: "5", Huge: "7" };

const FuturisticToolbar = ({
  applyStyle,
  applyLineHeight,
  activeStyles,
  onOpenLinkModal,
  onImageUpload,
  onDocxUpload,
  onVideoModalOpen,
  onTableModalOpen,
  onAiRefactor,
  content,
  isGenerating,
  isAiLoadingMinimized,
}) => {
  const imageFileInputRef = useRef(null);
  const docxFileInputRef = useRef(null);
  const toolbarRef = useRef(null);

  // --- Dragging Logic (Corrected) ---
  const [position, setPosition] = useState({ x: 50, y: 50 });
  const [isDragging, setIsDragging] = useState(false);
  const dragOffset = useRef({ x: 0, y: 0 });

  const onDragStart = (e) => {
    if (e.button !== 0) return;
    setIsDragging(true);
    dragOffset.current = {
      x: e.clientX - position.x,
      y: e.clientY - position.y,
    };
    e.preventDefault();
  };

  useEffect(() => {
    const onDragMove = (e) => {
      if (!isDragging) return;
      setPosition({
        x: e.clientX - dragOffset.current.x,
        y: e.clientY - dragOffset.current.y,
      });
    };
    const onDragEnd = () => setIsDragging(false);

    window.addEventListener("mousemove", onDragMove);
    window.addEventListener("mouseup", onDragEnd);

    return () => {
      window.removeEventListener("mousemove", onDragMove);
      window.removeEventListener("mouseup", onDragEnd);
    };
  }, [isDragging]);

  return (
    <>
      <FuturisticStyle />
      <div
        ref={toolbarRef}
        className={`futuristic-toolbar ${isDragging ? "" : ""}`}
        style={{ top: `${position.y}px`, left: `${position.x}px` }}
      >
        <div
          className="drag-handle"
          onMouseDown={onDragStart}
          title="Drag Toolbar"
        >
          <GripVertical size={20} />
        </div>
        <div className="toolbar-content">
          {/* Undo/Redo */}
          <div className="toolbar-group">
            <FuturisticToolbarButton
              title="Undo"
              onClick={() => applyStyle("undo")}
            >
              <Undo size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Redo"
              onClick={() => applyStyle("redo")}
            >
              <Redo size={18} />
            </FuturisticToolbarButton>
          </div>
          <div className="toolbar-separator" />

          {/* Font & Text Style */}
          <div className="toolbar-group">
            <FuturisticDropdown title="Headings" icon={<Pilcrow size={18} />}>
              {Object.entries(HEADINGS).map(([k, v]) => (
                <button
                  key={k}
                  className="futuristic-dropdown-item"
                  onClick={() => applyStyle("formatBlock", v)}
                >
                  {k}
                </button>
              ))}
            </FuturisticDropdown>
            <FuturisticDropdown title="Font" icon={<Type size={18} />}>
              {FONT_FACES.map((f) => (
                <button
                  key={f}
                  className="futuristic-dropdown-item"
                  style={{ fontFamily: f }}
                  onClick={() => applyStyle("fontName", f)}
                >
                  {f}
                </button>
              ))}
            </FuturisticDropdown>
            <FuturisticDropdown title="Font Size" icon={<Type size={14} />}>
              {Object.entries(FONT_SIZES).map(([key, value]) => (
                <button
                  key={key}
                  className="forma-text-dropdown-item"
                  onClick={() => applyStyle("fontSize", value)}
                >
                  {key}
                </button>
              ))}
            </FuturisticDropdown>

            <FuturisticDropdown
              title="Line Height"
              icon={<ArrowDownUp size={18} />}
            >
              {Object.entries(LINE_HEIGHTS).map(([k, v]) => (
                <button
                  key={k}
                  className="futuristic-dropdown-item"
                  onClick={() => applyLineHeight(v)}
                >
                  {k}
                </button>
              ))}
            </FuturisticDropdown>
          </div>
          <div className="toolbar-separator" />

          {/* Basic Formatting */}
          <div className="toolbar-group">
            <FuturisticToolbarButton
              title="Bold"
              cmd="bold"
              active={activeStyles}
              onClick={() => applyStyle("bold")}
            >
              <Bold size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Italic"
              cmd="italic"
              active={activeStyles}
              onClick={() => applyStyle("italic")}
            >
              <Italic size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Underline"
              cmd="underline"
              active={activeStyles}
              onClick={() => applyStyle("underline")}
            >
              <Underline size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Strikethrough"
              cmd="strikeThrough"
              active={activeStyles}
              onClick={() => applyStyle("strikeThrough")}
            >
              <Strikethrough size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Superscript"
              cmd="superscript"
              active={activeStyles}
              onClick={() => applyStyle("superscript")}
            >
              <Superscript size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Subscript"
              cmd="subscript"
              active={activeStyles}
              onClick={() => applyStyle("subscript")}
            >
              <Subscript size={18} />
            </FuturisticToolbarButton>
            <FuturisticColorPicker
              command="foreColor"
              applyStyle={applyStyle}
            />
            <FuturisticColorPicker
              command="backColor"
              applyStyle={applyStyle}
            />
          </div>
          <div className="toolbar-separator" />

          {/* Alignment & Lists */}
          <div className="toolbar-group">
            <FuturisticToolbarButton
              title="Align Left"
              cmd="justifyLeft"
              active={activeStyles}
              onClick={() => applyStyle("justifyLeft")}
            >
              <AlignLeft size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Align Center"
              cmd="justifyCenter"
              active={activeStyles}
              onClick={() => applyStyle("justifyCenter")}
            >
              <AlignCenter size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Align Right"
              cmd="justifyRight"
              active={activeStyles}
              onClick={() => applyStyle("justifyRight")}
            >
              <AlignRight size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Ordered List"
              cmd="insertOrderedList"
              active={activeStyles}
              onClick={() => applyStyle("insertOrderedList")}
            >
              <ListOrdered size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Unordered List"
              cmd="insertUnorderedList"
              active={activeStyles}
              onClick={() => applyStyle("insertUnorderedList")}
            >
              <List size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Outdent"
              onClick={() => applyStyle("outdent")}
            >
              <Outdent size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Indent"
              onClick={() => applyStyle("indent")}
            >
              <Indent size={18} />
            </FuturisticToolbarButton>
          </div>
          <div className="toolbar-separator" />

          {/* Insert Elements */}
          <div className="toolbar-group">
            <FuturisticToolbarButton
              title="Add/Edit Link"
              onClick={onOpenLinkModal}
            >
              <Link size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Blockquote"
              onClick={() => applyStyle("formatBlock", "<blockquote>")}
            >
              <Quote size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Code Block"
              onClick={() => applyStyle("formatBlock", "<pre>")}
            >
              <Code size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Horizontal Line"
              onClick={() => applyStyle("insertHorizontalRule")}
            >
              <Minus size={18} />
            </FuturisticToolbarButton>
          </div>
          <div className="toolbar-separator" />

          {/* Media & Advanced */}
          <div className="toolbar-group">
            <FuturisticToolbarButton
              title="Insert Image"
              onClick={() => imageFileInputRef.current?.click()}
            >
              <Image size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Insert Video"
              onClick={onVideoModalOpen}
            >
              <Video size={18} />
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Insert Table"
              onClick={onTableModalOpen}
            >
              <Table size={18} />
            </FuturisticToolbarButton>

            <FuturisticToolbarButton
              title="Import from Word"
              onClick={() => docxFileInputRef.current?.click()}
            >
              <FileUp size={18} />
            </FuturisticToolbarButton>
            <FuturisticEmojiPicker applyStyle={applyStyle} />
          </div>
          <div className="toolbar-separator" />

          {/* AI & Clear */}
          <div className="toolbar-group">
            <FuturisticToolbarButton
              title="Improve with AI"
              onClick={onAiRefactor}
              disabled={!content || isGenerating}
              className="ai-button"
            >
              {isGenerating ? (
                <div className="spinner"></div>
              ) : (
                <Sparkles size={18} />
              )}
            </FuturisticToolbarButton>
            <FuturisticToolbarButton
              title="Clear Formatting"
              onClick={() => applyStyle("removeFormat")}
            >
              <Eraser size={18} />
            </FuturisticToolbarButton>
          </div>
        </div>
      </div>
      <input
        type="file"
        accept="image/*"
        ref={imageFileInputRef}
        onChange={onImageUpload}
        style={{ display: "none" }}
      />
      <input
        type="file"
        accept=".docx"
        ref={docxFileInputRef}
        onChange={onDocxUpload}
        style={{ display: "none" }}
      />
    </>
  );
};

FuturisticToolbar.propTypes = {
  applyStyle: PropTypes.func.isRequired,
  activeStyles: PropTypes.object.isRequired,
  applyLineHeight: PropTypes.func.isRequired,
  onOpenLinkModal: PropTypes.func.isRequired,
  onImageUpload: PropTypes.func.isRequired,
  onDocxUpload: PropTypes.func.isRequired,
  onVideoModalOpen: PropTypes.func.isRequired,
  onTableModalOpen: PropTypes.func.isRequired,
  onAiRefactor: PropTypes.func.isRequired,
  content: PropTypes.string,
  isGenerating: PropTypes.bool.isRequired,
  isAiLoadingMinimized: PropTypes.bool.isRequired,
};

export default FuturisticToolbar;
