import PropTypes from "prop-types";
import { Palette, Highlighter } from "lucide-react";
import Dropdown from "./Dropdown";
import ColorPickerStyles from "./styles/ColorPicker.styles";

// An array of hex color codes for the picker
const COLORS = [
  "#000000",
  "#222222",
  "#444444",
  "#666666",
  "#888888",
  "#999999",
  "#BBBBBB",
  "#CCCCCC",
  "#DDDDDD",
  "#EEEEEE",
  "#FFFFFF",
  "#FF0000",
  "#CC0000",
  "#990000",
  "#660000",
  "#FF6666",
  "#FF9900",
  "#FF6600",
  "#CC6600",
  "#FFB84D",
  "#FFFF00",
  "#FFEE00",
  "#CCCC00",
  "#999900",
  "#00FF00",
  "#00CC00",
  "#009900",
  "#66FF66",
  "#33CC33",
  "#00FFFF",
  "#00CCCC",
  "#009999",
  "#66FFFF",
  "#0000FF",
  "#0000CC",
  "#000099",
  "#6666FF",
  "#3366FF",
  "#9900FF",
  "#6600CC",
  "#9933FF",
  "#CC99FF",
  "#FF00FF",
  "#CC00CC",
  "#FF66FF",
  "#FF99FF",
];

/**
 * A color picker dropdown for text or highlight color.
 */
const ColorPicker = ({ command, applyStyle }) => {
  const title = command === "foreColor" ? "Text Color" : "Highlight Color";
  const icon =
    command === "foreColor" ? <Palette size={16} /> : <Highlighter size={16} />;

  return (
    <>
      <ColorPickerStyles />

      <Dropdown title={title} icon={icon}>
        <div className="forma-text-color-grid">
          {COLORS.map((color) => (
            <button
              key={color}
              type="button"
              className="forma-text-color-swatch"
              // The background color is dynamic, so it remains an inline style
              style={{ backgroundColor: color }}
              onClick={() => applyStyle(command, color)}
              aria-label={color}
            />
          ))}
        </div>
      </Dropdown>
    </>
  );
};

ColorPicker.propTypes = {
  command: PropTypes.oneOf(["foreColor", "backColor"]).isRequired,
  applyStyle: PropTypes.func.isRequired,
};

export default ColorPicker;
