const EditorStyles = () => (
  <style>{`
            .forma-text-source-view {
              min-height: 60vh;
              max-height: 75vh;
              resize: none;
              display: block;
              width: 98%;
              height: 100%;
            margin: auto;
              font-family: monospace;
              font-size: 0.875em;
              color: #f8f9fa;
              background-color: #212529;
              border: none;
              border-radius: 0;
              outline: none;
                   white-space: pre-wrap;
              word-wrap: break-word;
            overflow-x: hidden;
            }
            .forma-text-editor-content {
              min-height: 60vh;
              height: 100%;
              padding: 1rem;
              border: none;
              border-radius: 0;
              outline: none;
              white-space: pre-wrap;
              word-wrap: break-word;
            }
          `}</style>
);

export default EditorStyles;
