import { useState } from "react";
import PropTypes from "prop-types";
import { Video, Link } from "lucide-react";
import BaseModal from "./BaseModal";
import VideoModalStyles from "./styles/VideoModal.styles";

const VideoModal = ({ isOpen, onClose, onInsert }) => {
  const [videoUrl, setVideoUrl] = useState("");

  const handleInsert = () => {
    onInsert(videoUrl);
    setVideoUrl("");
  };

  if (!isOpen) return null;

  return (
    <>
    <VideoModalStyles/>
      <BaseModal
        onClose={onClose}
        title="Insert Video"
        icon={<Video size={20} />}
      >
        <div className="forma-text-video-modal-body">
          <label className="forma-text-video-label">Video URL</label>
          <div className="forma-text-video-input-group">
            <span className="forma-text-video-input-icon">
              <Link size={16} />
            </span>
            <input
              type="url"
              className="forma-text-video-input"
              value={videoUrl}
              onChange={(e) => setVideoUrl(e.target.value)}
              placeholder="e.g., https://www.youtube.com/watch?v=..."
              autoFocus
            />
          </div>
          <div className="forma-text-video-form-text">
            Supports YouTube and Vimeo links.
          </div>
        </div>
        <div className="forma-text-video-modal-footer">
          <button
            type="button"
            className="forma-text-video-button forma-text-video-button--secondary"
            onClick={onClose}
          >
            Cancel
          </button>
          <button
            type="button"
            className="forma-text-video-button forma-text-video-button--primary"
            onClick={handleInsert}
          >
            Insert Video
          </button>
        </div>
      </BaseModal>
    </>
  );
};

VideoModal.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  onInsert: PropTypes.func.isRequired,
};

export default VideoModal;