import { useRef } from "react";
import PropTypes from "prop-types";
import {
  Bold,
  Italic,
  Underline,
  Strikethrough,
  Code,
  Link,
  Image,
  List,
  ListOrdered,
  Quote,
  AlignLeft,
  AlignCenter,
  AlignRight,
  Indent,
  Outdent,
  Table,
  Pilcrow,
  Eraser,
  Undo,
  Redo,
  Minus,
  Type,
  ArrowDownUp,
  FileUp,
  Superscript,
  Subscript,
  Video,
  Sparkles,
} from "lucide-react";
import ToolbarButton from "./components/ToolbarButton";
import Dropdown from "./components/Dropdown";
import ColorPicker from "./components/ColorPicker";
import EmojiPicker from "./components/EmojiPicker";
import ClassicToolbarStyles from "./ClassicToolbar.styles";

const FONT_FACES = [
  "Arial",
  "Georgia",
  "Helvetica",
  "Tahoma",
  "Times New Roman",
  "Verdana",
  "Courier New",
];
const FONT_SIZES = { Small: "2", Normal: "3", Large: "5", Huge: "7" };
const HEADINGS = {
  "Heading 1": "<h1>",
  "Heading 2": "<h2>",
  "Heading 3": "<h3>",
  "Heading 4": "<h4>",
  "Heading 5": "<h5>",
  Paragraph: "<p>",
};
const LINE_HEIGHTS = { Single: "1", 1.15: "1.15", 1.5: "1.5", Double: "2" };

const ClassicToolbar = ({
  applyStyle,
  applyLineHeight,
  activeStyles,
  onOpenLinkModal,
  onImageUpload,
  onDocxUpload,
  onVideoModalOpen,
  onTableModalOpen,
  onAiRefactor,
  content,
  isGenerating,
  isAiLoadingMinimized,
}) => {
  const imageFileInputRef = useRef(null);
  const docxFileInputRef = useRef(null);

  return (
    <>
      <ClassicToolbarStyles />

      <div className="forma-text-classic-toolbar">
        {/* Undo/Redo */}
        <div className="forma-text-btn-group">
          <ToolbarButton
            title="Undo (Ctrl+Z)"
            onClick={() => applyStyle("undo")}
          >
            <Undo size={16} />
          </ToolbarButton>
          <ToolbarButton
            title="Redo (Ctrl+Y)"
            onClick={() => applyStyle("redo")}
          >
            <Redo size={16} />
          </ToolbarButton>
        </div>

        {/* Font & Text Style Dropdowns */}
        <div className="forma-text-btn-group">
          <Dropdown title="Headings" icon={<Pilcrow size={14} />}>
            {Object.entries(HEADINGS).map(([key, value]) => (
              <button
                key={key}
                className="forma-text-dropdown-item"
                onClick={() => applyStyle("formatBlock", value)}
              >
                {key}
              </button>
            ))}
          </Dropdown>
          <Dropdown
            title="Font Family"
            icon={<span className="forma-text-font-icon">Aa</span>}
          >
            {FONT_FACES.map((face) => (
              <button
                key={face}
                className="forma-text-dropdown-item"
                style={{ fontFamily: face }}
                onClick={() => applyStyle("fontName", face)}
              >
                {face}
              </button>
            ))}
          </Dropdown>
          <Dropdown title="Font Size" icon={<Type size={14} />}>
            {Object.entries(FONT_SIZES).map(([key, value]) => (
              <button
                key={key}
                className="forma-text-dropdown-item"
                onClick={() => applyStyle("fontSize", value)}
              >
                {key}
              </button>
            ))}
          </Dropdown>
          <Dropdown title="Line Height" icon={<ArrowDownUp size={14} />}>
            {Object.entries(LINE_HEIGHTS).map(([key, value]) => (
              <button
                key={key}
                className="forma-text-dropdown-item"
                onClick={() => applyLineHeight(value)}
              >
                {key}
              </button>
            ))}
            <button
              className="forma-text-dropdown-item forma-text-dropdown-item--danger"
              onClick={() => applyLineHeight("normal")}
            >
              Remove Height
            </button>
          </Dropdown>
        </div>

        {/* Basic Formatting */}
        <div className="forma-text-btn-group">
          <ToolbarButton
            title="Bold (Ctrl+B)"
            command="bold"
            activeStyles={activeStyles}
            onClick={() => applyStyle("bold")}
          >
            <Bold size={14} />
          </ToolbarButton>
          <ToolbarButton
            title="Italic (Ctrl+I)"
            command="italic"
            activeStyles={activeStyles}
            onClick={() => applyStyle("italic")}
          >
            <Italic size={16} />
          </ToolbarButton>
          <ToolbarButton
            title="Underline (Ctrl+U)"
            command="underline"
            activeStyles={activeStyles}
            onClick={() => applyStyle("underline")}
          >
            <Underline size={16} />
          </ToolbarButton>
          <ToolbarButton
            title="Strikethrough"
            command="strikeThrough"
            activeStyles={activeStyles}
            onClick={() => applyStyle("strikeThrough")}
          >
            <Strikethrough size={16} />
          </ToolbarButton>
          <ToolbarButton
            title="Superscript"
            command="superscript"
            activeStyles={activeStyles}
            onClick={() => applyStyle("superscript")}
          >
            <Superscript size={16} />
          </ToolbarButton>
          <ToolbarButton
            title="Subscript"
            command="subscript"
            activeStyles={activeStyles}
            onClick={() => applyStyle("subscript")}
          >
            <Subscript size={16} />
          </ToolbarButton>
          <EmojiPicker applyStyle={applyStyle} />
        </div>

        {/* Color Pickers */}
        <div className="forma-text-btn-group">
          <ColorPicker command="foreColor" applyStyle={applyStyle} />
          <ColorPicker command="backColor" applyStyle={applyStyle} />
        </div>

        {/* Alignment */}
        <div className="forma-text-btn-group">
          <ToolbarButton
            title="Align Left"
            command="justifyLeft"
            activeStyles={activeStyles}
            onClick={() => applyStyle("justifyLeft")}
          >
            <AlignLeft size={16} />
          </ToolbarButton>
          <ToolbarButton
            title="Align Center"
            command="justifyCenter"
            activeStyles={activeStyles}
            onClick={() => applyStyle("justifyCenter")}
          >
            <AlignCenter size={16} />
          </ToolbarButton>
          <ToolbarButton
            title="Align Right"
            command="justifyRight"
            activeStyles={activeStyles}
            onClick={() => applyStyle("justifyRight")}
          >
            <AlignRight size={16} />
          </ToolbarButton>
        </div>

        {/* Lists & Indentation */}
        <div className="forma-text-btn-group">
          <ToolbarButton
            title="Ordered List"
            command="insertOrderedList"
            activeStyles={activeStyles}
            onClick={() => applyStyle("insertOrderedList")}
          >
            <ListOrdered size={16} />
          </ToolbarButton>
          <ToolbarButton
            title="Unordered List"
            command="insertUnorderedList"
            activeStyles={activeStyles}
            onClick={() => applyStyle("insertUnorderedList")}
          >
            <List size={16} />
          </ToolbarButton>
          <ToolbarButton title="Outdent" onClick={() => applyStyle("outdent")}>
            <Outdent size={16} />
          </ToolbarButton>
          <ToolbarButton title="Indent" onClick={() => applyStyle("indent")}>
            <Indent size={16} />
          </ToolbarButton>
        </div>

        {/* Insert Elements */}
        <div className="forma-text-btn-group">
          <ToolbarButton
            title="Blockquote"
            onClick={() => applyStyle("formatBlock", "<blockquote>")}
          >
            <Quote size={16} />
          </ToolbarButton>
          <ToolbarButton
            title="Code Block"
            onClick={() => applyStyle("formatBlock", "<pre>")}
          >
            <Code size={16} />
          </ToolbarButton>
          <ToolbarButton
            title="Add/Edit Link (Ctrl+K)"
            onClick={onOpenLinkModal}
          >
            <Link size={16} />
          </ToolbarButton>
          <ToolbarButton
            title="Horizontal Line"
            onClick={() => applyStyle("insertHorizontalRule")}
          >
            <Minus size={16} />
          </ToolbarButton>
          <ToolbarButton
            title="Clear Formatting"
            onClick={() => applyStyle("removeFormat")}
          >
            <Eraser size={16} />
          </ToolbarButton>
        </div>

        {/* Media & Advanced */}
        <div className="forma-text-btn-group">
          <ToolbarButton
            title="Insert Image"
            onClick={() => imageFileInputRef.current?.click()}
          >
            <Image size={16} />
          </ToolbarButton>
          <ToolbarButton title="Insert Video" onClick={onVideoModalOpen}>
            <Video size={16} />
          </ToolbarButton>
          <ToolbarButton title="Insert Table" onClick={onTableModalOpen}>
            <Table size={16} />
          </ToolbarButton>
          {/* <Dropdown title="Insert Columns" icon={<Columns size={16} />}>
            {Object.entries(COLUMN_LAYOUTS).map(([key, value]) => (
              <button key={key} className="forma-text-dropdown-item" onClick={() => applyStyle("insertHTML", value + "<p><br></p>")}>{key}</button>
            ))}
          </Dropdown> */}
          <ToolbarButton
            title="Import from Word (.docx)"
            onClick={() => docxFileInputRef.current?.click()}
          >
            <FileUp size={16} />
          </ToolbarButton>
        </div>

        {/* AI Feature */}
        <div className="forma-text-btn-group">
          <ToolbarButton
            title="Improve with AI"
            onClick={onAiRefactor}
            disabled={!content || content === "" || isGenerating}
          >
            {isGenerating && !isAiLoadingMinimized ? (
              <div className="forma-text-spinner" role="status"></div>
            ) : (
              <Sparkles size={16} />
            )}
          </ToolbarButton>
        </div>
      </div>

      {/* Hidden file inputs */}
      <input
        type="file"
        accept="image/*"
        ref={imageFileInputRef}
        onChange={onImageUpload}
        className="forma-text-hidden"
      />
      <input
        type="file"
        accept=".docx"
        ref={docxFileInputRef}
        onChange={onDocxUpload}
        className="forma-text-hidden"
      />
    </>
  );
};

ClassicToolbar.propTypes = {
  applyStyle: PropTypes.func.isRequired,
  applyLineHeight: PropTypes.func.isRequired,
  activeStyles: PropTypes.object.isRequired,
  onOpenLinkModal: PropTypes.func.isRequired,
  onImageUpload: PropTypes.func.isRequired,
  onDocxUpload: PropTypes.func.isRequired,
  onVideoModalOpen: PropTypes.func.isRequired,
  onTableModalOpen: PropTypes.func.isRequired,
  onAiRefactor: PropTypes.func.isRequired,
  content: PropTypes.string,
  isGenerating: PropTypes.bool.isRequired,
  isAiLoadingMinimized: PropTypes.bool.isRequired,
};

export default ClassicToolbar;
